import React, { useState } from "react";
import './Home.css';
import img from '../../img/kali4.jpg';
import King from '../../img/King.png';
import { Button, TextField } from "@mui/material";

const Home = () =>{
    
    const [show ,setShow] = useState(false);
    const [sco ,setSco] = useState();

    const clicked = () =>{
          setShow(true);
          setSco(window.scrollY+50);
    }

    console.log(sco)

    function setFixed()
    {
        if(window.scrollY>=sco)
        {
            setShow(false);
        }
    }
    window.addEventListener("scroll",setFixed);
   
    return(
        <div className="H-container" id="Profile">
{/* -------------  Left Side  ------------- */}
            <div className="H-left">
                <div className="H-left-1">
                    <h4>Hi There !!</h4>
                    <div style={{display:'flex',flexDirection:'row',gap:'2vw'}}><h3>I'm</h3><h1>Kaliyappan</h1></div>
                    <p> IAm a web designer and web developer ,then i creating high quality websites for all types of business,</p>
                    <div className="btns">
                        <button onClick={()=>clicked()}>Touch Me</button>
                        <button>Get Resume</button>
                    </div>
                </div> 
            </div>


{/* -------------  Right Side  ------------- */}

            <div className="H-right">
                <img src={img}/>
                {show ? <div className="forms" id="forms">
                    <TextField variant="outlined" fullWidth size="small" label="Name"/><br/><br/>
                    <TextField variant="outlined" fullWidth size="small" label="Phone Number"/><br/><br/>
                    <TextField variant="outlined" fullWidth size="small" label="email"/><br/><br/>
                    <TextField variant="outlined" fullWidth size="small" label="Subject"/><br/><br/>
                    <TextField variant="outlined" fullWidth size="small" label="Say something"/><br/><br/>
                    <Button variant="contained">Send</Button>
                </div>:""}
            </div>
        </div>
    )
}
export default Home;