import React from "react";
import './Contacts.css';
import img from '../../img/Location.png';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Button, TextField } from "@mui/material";
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import InstagramIcon from '@mui/icons-material/Instagram';
import { WhatsApp } from "@mui/icons-material";
import MarkEmailReadRoundedIcon from '@mui/icons-material/MarkEmailReadRounded';
import TtyRoundedIcon from '@mui/icons-material/TtyRounded';

// import TextArea from "antd/lib/input/TextArea";


const Contacts = () =>{
    return(
        <div className="C-container" id="Contacts">
{/* -------------  Left Side  ------------- */}
            <div className="C-left">
                <img src={img}/>
                <div className="C-content">
                    <p style={{textDecoration:'underline'}}><MarkEmailReadRoundedIcon/>kalisoft117@gmail.com</p>
                    <p><TtyRoundedIcon/>9791745073 | 9626345073</p>
                </div>
            </div>

{/* -------------  Right Side  ------------- */}

            <div className="C-right">
                 <div>
                    <TextField size="small" fullWidth variant="outlined" label="Name" color="secondary"/><br/><br/>
                    <TextField size="small" fullWidth variant="outlined" label="Mobile Number" color="secondary"/><br/><br/>
                    <TextField size="small" fullWidth variant="outlined" label="Email" color="secondary"/><br/><br/>
                    <TextField size="small" fullWidth variant="outlined" label="Say Something" color="secondary" multiline rows={3}/><br/><br/>
                    <Button variant="contained" fullWidth >Send</Button>
                 </div>
            </div>
        </div>
    )
}
export default Contacts;